/**
 * Frontend styles for Reveal Block - Sticky Approach v4
 *
 * Fixed:
 * - Content doesn't peek early (full 100vh padding-top)
 * - Section height adapts to content height
 */

.caes-reveal {
	position: relative;
}

// Each frame section
.reveal-frame-section {
	position: relative;
	// Height will be set by JS based on content height
	// Fallback min-height if JS doesn't run
	min-height: 100vh;
}

// The sticky background wrapper
.reveal-frame-background {
    position: sticky;
    top: 0;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    z-index: 0;
    
    /* NEW: Hardware acceleration for smoother animations */
    will-change: transform, opacity;
    
    /* Ensure default state is full opacity unless JS changes it */
    opacity: 1; 
}

// Individual frame (image container)
.reveal-frame {
	position: absolute;
	inset: 0;
	margin: 0;

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: var(--desktop-focal-x, 50%) var(--desktop-focal-y, 50%);
	}

	.reveal-frame-desktop {
		display: block;
		object-position: var(--desktop-focal-x, 50%) var(--desktop-focal-y, 50%);
	}

	.reveal-frame-mobile {
		display: none;
		object-position: var(--mobile-focal-x, 50%) var(--mobile-focal-y, 50%);
	}

	.reveal-overlay {
		position: absolute;
		inset: 0;
		pointer-events: none;
		z-index: 2;
	}

	.reveal-frame-caption {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		padding: 1rem 1.5rem;
		background: rgba(0, 0, 0, 0.5);
		color: var(--wp--preset--color--base, #fff);
		line-height: 1.4;
		z-index: 3;

		.reveal-caption-wrap {
			max-width: var(--wp--style--global--wide-size, 1200px);
			margin: 0 auto;
		}

		.reveal-caption-desktop {
			display: block;
		}

		.reveal-caption-mobile {
			display: none;
		}
	}

	@media screen and (max-width: 768px) {
		img {
			object-position: var(--mobile-focal-x, 50%) var(--mobile-focal-y, 50%);
		}

		.reveal-frame-desktop {
			display: none;
		}

		.reveal-frame-mobile {
			display: block;
		}

		.reveal-frame-caption {
			.reveal-caption-desktop {
				display: none;
			}

			.reveal-caption-mobile {
				display: block;
			}
		}
	}
}

// Content that scrolls over the sticky background
.reveal-frame-content {
	position: relative;
	z-index: 1;
	
	// Pull up to overlap the sticky background
	margin-top: -100vh;
	
	// Full viewport padding so content starts completely off-screen at bottom
	padding-top: 100vh;
	
	// Padding on sides and bottom
	padding-left: 2rem;
	padding-right: 2rem;
	padding-bottom: 2rem;
	
	pointer-events: auto;
	
	> * {
		position: relative;
		z-index: 1;
	}
}

// Accessibility
.caes-reveal {
	a:focus,
	button:focus {
		outline: 2px solid currentColor;
		outline-offset: 2px;
	}
}

// Reduced motion
@media (prefers-reduced-motion: reduce) {
	.reveal-frame-content {
		opacity: 1 !important;
	}
}

// Print styles
@media print {
	.caes-reveal {
		.reveal-frame-section {
			min-height: auto !important;
			height: auto !important;
			page-break-inside: avoid;
		}
		
		.reveal-frame-background {
			position: relative;
			height: auto;
			min-height: 300px;
		}

		.reveal-frame-content {
			margin-top: 0;
			padding-top: 2rem;
		}

		.reveal-overlay {
			display: none;
		}
	}
}